import { NavLink } from "react-router-dom";
import InfoLogo from "../vectors/InfoLogo";

const Navbar = () => {
    return (
        <nav className="grid grid-cols-1 md:grid-cols-2 py-3 px-5 navbar bg-blue-800 items-center">
            <div className="m-auto md:m-0">
                <InfoLogo />
            </div>
            <div className="hidden md:flex justify-end items-center">
                <NavLink exact to="/d-bot-info" activeClassName="active">Info</NavLink>
                <NavLink exact to="/d-bot-info/faqs" activeClassName="active">Preguntas frecuentes</NavLink>
                <NavLink exact to="/d-bot-info/team" activeClassName="active">Equipo</NavLink>
            </div>

        </nav>
    );
}

export default Navbar;
