const Footer = () => {
    const [width, setWidth] = useState(window.innerWidth);
    const handleResize = (e) => setWidth(window.innerWidth)
    useEffect(() => {
        window.addEventListener('resize', handleResize)
        return () => {
            window.removeEventListener('resize', handleResize)
        };
    }, []);
    return (
        <div className="mt-32">
            <PuenteMaracaibo width={width} />
            <div className="bg-blue-800 border border-blue-800 p-4">
                <div className="grid grid-cols-1 mx-0 md:mx-2 text-white max-w-3xl m-auto">
                    <span className="text-2xl md:text-4xl font-medium">D-Bot</span>
                    <span className="text-lg md:text-2xl font-light">Una solución para automatizar</span>
                    <span className="text-xl md:text-3xl font-normal">Todo servicio al alcance de un mensaje</span>
                </div>
            </div>
            <div className="bg-blue-900 text-white p-4 ">
                <div className="max-w-3xl m-auto">
                    <div><p className="text-xl md:text-2xl text-center">Maracaibo Innova 2021</p></div>
                    <div><p className="text-xl md:text-2xl text-center">Universidad "Dr. José Gregorio Hernandez"</p></div>
                    <div className="w-full flex flex-col  md:flex-row justify-center p-4 md:p-0 text-white gap-1 my-4">

                        <div className="w-full md:w-3/12 grid grid-cols-1 mx-0 justify-center mb-4 md:mx-2 ">
                            <span className="text-xl"><p>Colaboradores:</p></span>
                            <span className="font-light">Ing. Marisela Zabala</span>
                            <span className="font-light">Diego Castillo</span>
                            <span className="font-light">José Hernandez</span>
                            <span className="font-light">Jesus Bohórquez</span>
                        </div>
                        <div className="w-full md:w-4/12 grid grid-cols-1 mx-0 md:mx-2">
                            <span className="text-xl font-medium">Contacto:</span>
                            <a href="https://www.linkedin.com/in/diegocashe/">
                                <i class="fab fa-linkedin mr-4"></i>
                                <span className="cursor-pointer underline">LinkedIn</span>
                            </a>
                            <a href="https://github.com/diegocashe">
                                <i className="fab fa-github mr-4"></i>
                                <span className="cursor-pointer underline">GitHub</span>
                            </a>
                            <a href="https://www.instagram.com/diegocashe/">
                                <i class="fab fa-instagram mr-4"></i>
                                <span className="cursor-pointer underline">Instagram</span>
                            </a>
                            <a href="/#">
                                <i className="fas fa-at mr-4"></i>
                                <span className="font-semibold">diegocashe17</span><span>@gmail.com</span>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
            <div className="bg-blueGray-900 md:h-1 h-32">

            </div>
        </div>
    );
}

export default Footer;
